const propertiesHandler = {
  computed: {
    properties() {
      let updatedProps = {};
      Object.keys(this.propsToMerge).forEach((propsToMergeComponentName) => {
        const currentComponentName = this.$options.name;
        if (currentComponentName === propsToMergeComponentName) updatedProps = this.propsToMerge[propsToMergeComponentName];
      });
      return { ...this.$props, ...updatedProps };
    },
  },
  props: {
    propsToMerge: {
      type: Object,
      default() {
        return {};
      },
    },
  },
};

export default propertiesHandler;
