<template lang="pug">
include ../../../mixins/bemto/bemto
.input-select
  +b.text.--p2.LABEL(v-if="label") {{label}}
  +b.text.--p2.SELECT(@input="inputHandler")
    option(v-for="(option,idx) in options" :key="idx" :selected="option.value==selected" :value="option.value") {{ option.title}}
</template>

<script>
import propertiesHandler from '@/mixins/propertiesHandler'
export default {
  mixins: [propertiesHandler],
  name: 'InputSelect',
  props: {
    selected: {
      type: String,
      default() {
        return '0'
      },
    },
    options: {
      type: Array,
      default() {
        return [
          { title: 'Title 1', value: '0', selected: true },
          { title: 'Title 2', value: '1', selected: false },
        ]
      },
    },
    label: {
      type: String,
      default() {
        return ''
      },
    },
  },
  methods: {
    inputHandler(event) {
      return this.$emit('change-handler', event.target.value)
    },
  },
}
</script>
